.layout {
  height: 100%;
  @media screen and (min-width: 1024px) {
    display: grid;
    grid-template-columns: 3fr 9fr;
  }
  .layoutBody {
    width: 100%;
    min-height: 100%;
    background: var(--bg-black-900);
    background: #161516;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5%;
    @media screen and (max-width: 1024px) {
      padding-top: 15%;
    }
  }
}
